  align-items: center;\n    line-height: normal;\n    position: relative;\n}\n\n// setting shake class to an item will give it a left ot right animation\n// used for the 'left' and 'rigth' arrows when dragging columns and scrolling\n.ag-shake-left-to-right {\n    animation-direction: alternate;\n    animation-duration: 0.2s;\n    animation-iteration-count: infinite;\n    animation-name: ag-shake-left-to-right;\n}\n\n@keyframes ag-shake-left-to-right {\n    from {\n        padding-left: 6px;\n        padding-right: 2px;\n    }\n\n    to {\n        padding-left: 2px;\n        padding-right: 6px;\n    }\n}\n\n.ag-root-wrapper {\n    cursor: default;\n    position: relative; // set to relative, so absolute popups appear relative to this\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n\n    &.ag-layout-normal {\n        height: 100%;\n    }\n}\n\n.ag-watermark {\n    position: absolute;\n    bottom: 10px;\n    right: 25px;\n    opacity: 0.5;\n    transition: opacity 1s ease-out 3s;\n    &:before {\n        content: '';\n        background-image: url(data:image/svg+xml;base64,);\n        filter: invert(50%);\n        background-repeat: no-repeat;\n        background-size: 110px 60px;\n        display: block;\n        height: 60px;\n        width: 110px;\n    }\n}\n\n.ag-watermark-text {\n    opacity: 0.5;\n    font-weight: bold;\n    font-family: Impact, sans-serif;\n    font-size: 19px;\n}\n\n.ag-root-wrapper-body {\n    display: flex;\n    flex-direction: row;\n\n    &.ag-layout-normal {\n        flex: 1 1 auto;\n        height: 0;\n        min-height: 0;\n    }\n}\n\n.ag-root {\n    position: relative; // set to relative, so absolute popups appear relative to this\n    display: flex;\n    flex-direction: column;\n\n    &.ag-layout-normal,\n    &.ag-layout-auto-height {\n        overflow: hidden; // was getting some 'shouldn't be there' scrolls, this sorts it out\n        flex: 1 1 auto;\n        width: 0;\n    }\n\n    &.ag-layout-normal {\n        height: 100%;\n    }\n}\n\n/**\n ****************************\n * Viewports\n ****************************\n*/\n.ag-header-viewport,\n.ag-floating-top-viewport,\n.ag-body-viewport,\n.ag-center-cols-viewport,\n.ag-floating-bottom-viewport,\n.ag-body-horizontal-scroll-viewport,\n.ag-virtual-list-viewport {\n    position: relative;\n    height: 100%;\n    min-width: 0px;\n    overflow: hidden;\n    flex: 1 1 auto;\n}\n\n.ag-body-viewport.ag-layout-print {\n    flex: none;\n}\n\n@media print {\n    .ag-layout-normal {\n        // hide grid if trying to print in non-print mode\n        display: none;\n    }\n    .ag-root-wrapper,\n    .ag-root-wrapper-body,\n    .ag-root,\n    .ag-body-viewport,\n    .ag-center-cols-container,\n    .ag-center-cols-viewport,\n    .ag-center-cols-clipper,\n    .ag-body-horizontal-scroll-viewport,\n    .ag-virtual-list-viewport {\n        // Need auto height because 100% height elements with overflow hidden cause printing issues in IE and Edge\n        height: auto !important;\n        // Overflow hidden, because otherwise scroll bars print in IE\n        overflow: hidden !important;\n        // flex elements cause printing issues in Firefox\n        // https://bugzilla.mozilla.org/show_bug.cgi?id=939897\n        display: block !important;\n    }\n    .ag-row {\n        page-break-inside: avoid;\n    }\n}\n\n.ag-body-viewport {\n    display: flex;\n\n    &.ag-layout-normal {\n        overflow-y: auto;\n        -webkit-overflow-scrolling: touch;\n    }\n}\n\n.ag-center-cols-viewport {\n    width: 100%;\n    overflow-x: auto;\n}\n\n.ag-body-horizontal-scroll-viewport {\n    overflow-x: scroll;\n}\n\n.ag-virtual-list-viewport {\n    overflow: auto;\n    width: 100%;\n}\n\n/**\n ****************************\n * Containers\n ****************************\n*/\n.ag-header-container,\n.ag-floating-top-container,\n.ag-body-container,\n.ag-pinned-right-cols-container,\n.ag-center-cols-container,\n.ag-pinned-left-cols-container,\n.ag-floating-bottom-container,\n.ag-body-horizontal-scroll-container,\n.ag-full-width-container,\n.ag-floating-bottom-full-width-container,\n.ag-virtual-list-container {\n    position: relative;\n}\n\n// for when auto height is used but there is no row data\n.ag-header-container, .ag-floating-top-container, .ag-floating-bottom-container {\n    height: 100%;\n    white-space: nowrap;\n}\n\n.ag-center-cols-clipper {\n    min-height: 100%;\n}\n\n.ag-center-cols-container {\n    display: block;\n}\n\n\n.ag-layout-print .ag-center-cols-clipper {\n    min-width: 100%;\n}\n\n.ag-layout-auto-height .ag-center-cols-clipper,\n.ag-layout-auto-height .ag-center-cols-container {\n    min-height: 50px;\n}\n\n.ag-pinned-right-cols-container {\n    display: block;\n}\n\n.ag-body-horizontal-scroll-container {\n    height: 100%;\n}\n\n.ag-full-width-container,\n.ag-floating-top-full-width-container,\n.ag-floating-bottom-full-width-container {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    // turn off pointer events, because this container overlays the main row containers.\n    // so when user clicks on space between full width rows, we want the mouse clicks to\n    // pass onto the underlying container where the real rows are. eg if using full width\n    // for row grouping, the groups will be in the full width container, but when user\n    // opens a group the children are shown in the other containers - we want to make sure we\n    // don't block mouse clicks to those other containers with the children.\n    pointer-events: none;\n}\n.ag-full-width-container {\n    width: 100%;\n}\n\n.ag-floating-bottom-full-width-container, .ag-floating-top-full-width-container {\n    display: inline-block;\n    overflow: hidden;\n    height: 100%;\n    width: 100%;\n}\n\n.ag-virtual-list-container {\n    overflow: hidden;\n}\n\n/**\n ****************************\n * Scrollers\n ****************************\n*/\n.ag-center-cols-clipper {\n    flex: 1;\n    min-width: 0;\n    overflow: hidden;\n}\n\n.ag-body-horizontal-scroll {\n    min-height: 0;\n    min-width: 0;\n    width: 100%;\n    display: flex;\n    position: relative;\n}\n\n.ag-layout-print {\n    .ag-body-horizontal-scroll {\n        display: none;\n    }\n}\n\n.ag-force-vertical-scroll {\n    overflow-y: scroll !important;\n\n    &.ag-layout-print {\n        overflow-y: visible !important;\n    }\n}\n\n.ag-horizontal-left-spacer, .ag-horizontal-right-spacer {\n    height: 100%;\n    min-width: 0;\n    overflow-x: scroll;\n    &.ag-scroller-corner {\n        overflow-x: hidden;\n    }\n}\n\n/**\n ****************************\n * Headers\n ****************************\n*/\n.ag-header, .ag-pinned-left-header, .ag-pinned-right-header {\n    display: inline-block;\n    overflow: hidden;\n    position: relative;\n}\n\n.ag-header-cell-sortable {\n    cursor: pointer;\n}\n\n.ag-header {\n    display: flex;\n    width: 100%;\n    white-space: nowrap;\n}\n\n.ag-pinned-left-header {\n    height: 100%;\n}\n\n.ag-pinned-right-header {\n    height: 100%;\n}\n\n.ag-header-row {\n    position: absolute;\n    overflow: hidden; // so when floating filters are height 0px, the contents don't spill out\n}\n\n.ag-header-cell {\n    display: inline-flex;\n    align-items: center;\n    position: absolute;\n    height: 100%;\n}\n\n.ag-header-group-cell-label, .ag-header-cell-label {\n    display: flex;\n    flex: 1 1 auto;\n    overflow: hidden;\n    align-items: center;\n    text-overflow: ellipsis;\n    align-self: stretch;\n}\n\n.ag-header-cell-text {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.ag-right-aligned-header .ag-header-cell-label {\n    flex-direction: row-reverse;\n}\n\n.ag-header-group-text {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.ag-header-cell-resize {\n    position: absolute;\n    z-index: 2;\n    height: 100%;\n    width: 8px;\n    top: 0;\n\n    cursor: ew-resize;\n\n    // unpinned headers get their rezise handle on the right in normal mode and left in RTL mode\n    @include ag-unthemed-rtl((\n        right: -4px\n    ));\n}\n\n.ag-pinned-left-header .ag-header-cell-resize {\n    right: -4px; // pinned left headers always have their resize on the right, even in RTL mode\n}\n\n.ag-pinned-right-header .ag-header-cell-resize {\n    left: -4px; // pinned right headers always have their resize on the left, even in RTL mode\n}\n\n.ag-header-select-all {\n    display: flex;\n}\n\n/**\n ****************************\n * Columns\n ****************************\n*/\n.ag-column-moving {\n    .ag-cell {\n        transition: left 0.2s;\n    }\n\n    .ag-header-cell {\n        transition: left 0.2s;\n    }\n\n    .ag-header-group-cell {\n        transition: left 0.2s, width 0.2s;\n    }\n}\n\n/**\n ****************************\n * Column Panel\n ****************************\n*/\n\n.ag-column-panel {\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    flex: 1 1 auto;\n}\n\n.ag-column-select {\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    flex-grow: 3;\n    flex-shrink: 1;\n    flex-basis: 0;\n}\n\n.ag-column-select-header {\n    position: relative;\n    display: flex;\n    flex: none;\n}\n\n.ag-column-select-header-icon {\n    position: relative;\n}\n\n.ag-column-select-header-filter-wrapper {\n    flex: 1 1 auto;\n}\n\n.ag-column-select-header-filter {\n    width: 100%;\n}\n\n.ag-column-select-list {\n    flex: 1 1 auto;\n    overflow: auto;\n}\n\n.ag-column-drop {\n    display: inline-flex;\n    align-items: center;\n    overflow: auto;\n    width: 100%;\n}\n\n.ag-column-drop-list {\n    display: flex;\n    align-items: center;\n}\n\n.ag-column-drop-cell {\n    display: flex;\n    align-items: center;\n}\n\n.ag-column-drop-cell-text {\n    overflow: hidden;\n    flex: 1 1 auto;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.ag-column-drop-vertical {\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    flex: 1 1 0;\n    align-items: stretch;\n}\n\n.ag-column-drop-vertical-title-bar {\n    display: flex;\n    align-items: center;\n}\n\n.ag-column-drop-vertical-list {\n    align-items: stretch;\n    flex-grow: 1;\n    flex-direction: column;\n    overflow-x: auto;\n\n    > * {\n        flex: none;\n    }\n}\n\n.ag-column-drop-empty .ag-column-drop-vertical-list {\n    overflow: hidden;\n}\n\n.ag-column-drop-vertical-empty-message {\n    display: block;\n}\n\n.ag-column-drop.ag-column-drop-horizontal {\n    white-space: nowrap;\n    overflow: hidden;\n}\n\n.ag-column-drop-cell-button {\n    cursor: pointer;\n}\n\n.ag-filter-toolpanel {\n    flex: 1 1 0px;\n    min-width: 0;\n}\n\n.ag-filter-toolpanel-header {\n    position: relative;\n}\n\n.ag-filter-toolpanel-header, .ag-filter-toolpanel-search {\n    display: flex;\n    align-items: center;\n\n    > * {\n        display: flex;\n        align-items: center;\n    }\n}\n\n.ag-filter-apply-panel {\n    display: flex;\n    justify-content: flex-end;\n    overflow: hidden;\n}\n\n/**\n ****************************\n * Rows\n ****************************\n*/\n// for animations, allows rows to slide up and down,\n.ag-row-animation .ag-row {\n    transition: transform 0.4s, top 0.4s, height 0.4s, background-color 0.1s, opacity 0.2s;\n}\n\n.ag-row-no-animation .ag-row {\n    transition: background-color 0.1s;\n}\n\n.ag-row {\n    white-space: nowrap;\n    width: 100%;\n}\n\n.ag-row-loading {\n    display: flex;\n    align-items: center;\n}\n\n.ag-row-position-absolute {\n    position: absolute;\n}\n\n.ag-row-position-relative {\n    position: relative;\n}\n\n.ag-full-width-row {\n    overflow: hidden;\n    // turn events back on, as we removed them in the parent\n    pointer-events: all;\n}\n\n.ag-row-inline-editing {\n    z-index: 1;\n}\n\n.ag-row-dragging {\n    z-index: 2;\n}\n\n.ag-stub-cell {\n    display: flex;\n    align-items: center;\n}\n\n/**\n ****************************\n * Cells\n ****************************\n*/\n.ag-cell {\n    display: inline-block;\n    position: absolute;\n    white-space: nowrap;\n}\n\n.ag-cell-value, .ag-group-value {\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n.ag-cell-wrapper {\n    display: flex;\n    align-items: center;\n    height: 100%;\n    // adding width: 100% here breaks text-overflow: ellipsis\n    // width: 100%;\n    &.ag-row-group {\n        align-items: flex-start;\n    }\n}\n\n.ag-full-width-row .ag-cell-wrapper.ag-row-group {\n    align-items: center;\n}\n\n.ag-cell-auto-height {\n    height: 100%;\n}\n\n.ag-cell-inline-editing {\n    z-index: 1;\n    .ag-cell-edit-wrapper,\n    .ag-cell-editor,\n    .ag-cell-editor .ag-wrapper,\n    .ag-cell-editor input {\n        height: 100%;\n        width: 100%;\n        line-height: normal;\n    }\n}\n\n.ag-cell .ag-icon {\n    display: inline-block;\n    vertical-align: middle;\n}\n\n/**\n ****************************\n * Filters\n ****************************\n*/\n.ag-set-filter-item {\n    display: flex;\n    align-items: center;\n    height: 100%;\n}\n\n.ag-set-filter-item-value {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.ag-set-filter-item-checkbox {\n    display: flex;\n\n    // for focus reasons, we need this for IE, otherwise for the set filter, the 'selectAll' click doesn't work\n    // when you click on it when the quick filter has focus. this was a fix for AG-2258\n    pointer-events: none;\n}\n\n.ag-filter-body-wrapper {\n    display: flex;\n    flex-direction: column;\n}\n\n.ag-filter-filter {\n    flex: 1 1 auto;\n    overflow: hidden;\n}\n\n.ag-filter-condition {\n    display: flex;\n}\n\n/**\n ****************************\n * Floating Filter\n ****************************\n*/\n\n.ag-floating-filter-body {\n    position: relative;\n    display: flex;\n    flex: 1 1 auto;\n    height: 100%;\n}\n\n.ag-floating-filter-full-body {\n    display: flex;\n    flex: 1 1 auto;\n    height: 100%;\n    width: 100%;\n    align-items: center;\n    overflow: hidden;\n}\n\n.ag-floating-filter-full-body > div {\n    flex: 1 1 auto;\n}\n\n.ag-floating-filter {\n    display: inline-block;\n    position: absolute;\n}\n\n.ag-floating-filter-input {\n    align-items: center;\n    display: flex;\n    width: 100%;\n\n    > * {\n        flex: 1 1 auto;\n    }\n}\n\n.ag-floating-filter-button {\n    display: flex;\n    flex: none;\n}\n\n/**\n ****************************\n * Drag & Drop\n ****************************\n*/\n\n.ag-dnd-ghost {\n    position: absolute;\n    display: inline-flex;\n    align-items: center;\n    cursor: move;\n    white-space: nowrap;\n}\n\n/**\n ****************************\n * Overlay\n ****************************\n*/\n.ag-overlay {\n    height: 100%;\n    left: 0;\n    pointer-events: none;\n    position: absolute;\n    top: 0;\n    width: 100%;\n}\n\n.ag-overlay-panel {\n    display: flex;\n    height: 100%;\n    width: 100%;\n}\n\n.ag-overlay-wrapper {\n    display: flex;\n    flex: none;\n    width: 100%;\n    height: 100%;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n}\n\n.ag-overlay-loading-wrapper {\n    // prevent interaction with grid while it's loading\n    pointer-events: all;\n}\n\n/**\n ****************************\n * Popup\n ****************************\n*/\n.ag-popup-child {\n    z-index: 5;\n}\n\n.ag-popup-editor {\n    position: absolute;\n    user-select: none;\n    z-index: 1;\n}\n\n.ag-large-text-input {\n    display: block;\n}\n\n/**\n ****************************\n * Virtual Lists\n ****************************\n*/\n.ag-virtual-list-item {\n    position: absolute;\n    width: 100%;\n}\n\n/**\n ****************************\n * Floating Top and Bottom\n ****************************\n*/\n.ag-floating-top {\n    overflow: hidden;\n    white-space: nowrap;\n    width: 100%;\n    position: relative;\n    display: flex;\n}\n\n.ag-pinned-left-floating-top {\n    display: inline-block;\n    overflow: hidden;\n    position: relative;\n    min-width: 0px;\n}\n\n.ag-pinned-right-floating-top {\n    display: inline-block;\n    overflow: hidden;\n    position: relative;\n    min-width: 0px;\n}\n\n.ag-floating-bottom {\n    overflow: hidden;\n    white-space: nowrap;\n    width: 100%;\n    position: relative;\n    display: flex;\n}\n\n.ag-pinned-left-floating-bottom {\n    display: inline-block;\n    overflow: hidden;\n    position: relative;\n    min-width: 0px;\n}\n\n.ag-pinned-right-floating-bottom {\n    display: inline-block;\n    overflow: hidden;\n    position: relative;\n    min-width: 0px;\n}\n\n\n/**\n ****************************\n * Dialog\n ****************************\n*/\n\n.ag-dialog, .ag-panel {\n    display: flex;\n    flex-direction: column;\n    position: relative;\n    overflow: hidden;\n}\n\n.ag-panel-title-bar {\n    display: flex;\n    flex: none;\n    align-items: center;\n    cursor: default;\n}\n\n.ag-panel-title-bar-title {\n    flex: 1 1 auto;\n}\n\n.ag-panel-title-bar-buttons {\n    display: flex;\n}\n\n.ag-panel-title-bar-button {\n    cursor: pointer;\n}\n\n.ag-panel-content-wrapper {\n    display: flex;\n    flex: 1 1 auto;\n    position: relative;\n    overflow: hidden;\n}\n\n.ag-dialog {\n    position: absolute;\n}\n\n.ag-resizer {\n    position: absolute;\n    pointer-events: none;\n    z-index: 1;\n    &.ag-resizer-topLeft {\n        top: 0;\n        left: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nwse-resize;\n    }\n    &.ag-resizer-top {\n        top: 0;\n        left: 5px;\n        right: 5px;\n        height: 5px;\n        cursor: ns-resize;\n    }\n    &.ag-resizer-topRight {\n        top: 0;\n        right: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nesw-resize;\n    }\n    &.ag-resizer-right {\n        top: 5px;\n        right: 0;\n        bottom: 5px;\n        width: 5px;\n        cursor: ew-resize;\n    }\n    &.ag-resizer-bottomRight {\n        bottom: 0;\n        right: 0;\n        height: 5px;\n     